.products {
  padding-top: 121px;
  padding-bottom: 98px;
  @media screen and (min-width: 768px) {
    padding-top: 122px;
    padding-bottom: 112px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 127px;
    padding-bottom: 119px;
  }
}

.products__container {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media screen and (min-width: 768px) {
    justify-content: space-between;
    flex-direction: row;
    &:not(:last-child) {
      margin-right: 0px;
    }
  }
  @media screen and (min-width: 1280px) {
    &:not(:last-child) {
      margin-right: 0px;
    }
  }
}

.products__list {
  display: flex;
  justify-content: center;
  margin-top: 130px;
  position: relative;
  @media screen and (min-width: 768px) {
    margin-top: 150px;
    &:not(:last-child) {
      margin-right: 20px;
    }
  }
  @media screen and (min-width: 1280px) {
    margin-top: 259px;
    &:not(:last-child) {
      margin-right: 23px;
    }
  }
}

.ice_cream {
  background-color: $color-bg;
}

.ice__coffee {
  background-color: $color-bg-brown;
}

.milkshakes {
  background-color: $color-bg-green;
}

.ice_cream,
.ice__coffee,
.milkshakes {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 24px;
  padding: 160px 40px 40px 40px;
  @media screen and (max-width: 767px) and (max-width: 767px) {
    width: 280px;
    height: 485px;
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    padding: 150px 11px 40px 11px;
  }
  @media screen and (min-width: 1280px) {
    padding: 163px 44px 50px 44px;
  }
}

.product__images {
  position: absolute;
  top: 0;
  transform: translateY(-125px);
  @media screen and (min-width: 768px) {
    transform: translateY(-125px);
    width: 100%;
  }
  @media screen and (min-width: 1280px) {
    transform: translateY(-210px);
    width: 335px;
  }
}

.products__name {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $color-white;
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 34px;
  }
}

.product__dot {
  color: #fff;
  font-weight: 900;
  font-size: 30px;
  border-radius: 50%;
  margin-bottom: 30px;
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 34px;
  }
}

.products__description {
  font-family: DM Sans;
  font-style: normal;
  font-weight: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.52;
  text-align: center;
  letter-spacing: 0.04em;
  color: $color-white;
  margin-bottom: 30px;
  @media screen and (min-width: 768px) {
    line-height: 1.76;
    margin-bottom: 40px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    margin-bottom: 62px;
  }
}

.product__text {
  font-family: $font-family-second;
  margin-bottom: 15px;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  color: $color-bg-circle;
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 26px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 34px;
  }
}

.product__title {
  font-family: $font-family-second;
  font-size: 34px;
  line-height: 39px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $color-primary;
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 48px;
    line-height: 55px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 66px;
  }
}

.product__button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background-color: $color-white;
  color: $color-primary;
  &:hover,
  &:focus {
    background-color: $color-hover-btn-first;
  }
}

.product__icon {
  width: 18px;
  height: 18px;
  fill: currentColor;
}

.product__icon {
  fill: $color-hover-btn-first;
  .product__button:hover &,
  .product__button:focus &,
  .product__button:active & {
    fill: $color-white;
  }
}

/////////modals
.modal-overlay {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
  vertical-align: middle;
}

.modal {
  position: relative;
  max-width: 329px;
  height: 600px;
  margin: 0 auto;
  padding: 50px 15px 25px;
  z-index: 15;
  text-align: center;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 20px;
  @media screen and (max-width: 479px) {
    max-width: 220px;
    height: 425px;
  }
  @media screen and (max-width: 768px) {
    max-width: 220px;
    height: 500px;
    margin-left: 0 auto;
  }
}

.modal-overlay--visible {
  position: fixed;
  opacity: 1;
  visibility: visible;
  transition: all 0.3s ease-in-out;
}

.modal--visible {
  display: block;
}

.modal__coffee {
  background-color: #ffb8ca;
}

.modal__cream {
  background-color: #f0d1a5;
}

.modal__milcshakes {
  background-color: #c2e297;
}

.product__text-modal {
  font-family: $font-family-second;
  margin-bottom: 15px;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  color: $color-white;
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 26px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 34px;
  }
}

.modal__coffee-text {
  font-family: DM Sans;
  font-style: normal;
  font-weight: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.52;
  text-align: center;
  letter-spacing: 0.04em;
  color: $color-white;
  @media screen and (min-width: 768px) {
    line-height: 1.76;
    margin-bottom: 40px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    margin-bottom: 62px;
  }
}
